:root {
    --bs-nav-link-color: white;
    --bs-mobileNav-link-color:white;
    --bs-hover-color: #ad9772;
    --bs-nav-color: rgba(255, 255, 255, 0.3);
    --bs-scroll-nav-bg-color: rgba(254, 254, 254, 0.85);
    --bs-scroll-nav-link-color: rgba(0, 0, 0, 0.85);
    --bs-rwd-nav-bg-color: rgba(18, 18, 18, 0.7);
    --bs-hamburger-close: #000;
    --bs-hamburger-open: #fff;
    --bs-body-font-color:#fff;
    --bs-primary:#fff;
    --bs-secondary:orange
  }


/* 全背景圖片 */
body:before {
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url(../images/bg.jpg) no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  padding-bottom: 20%;
}



/* 預設文字顏色 */
body{
  color:var(--bs-body-font-color);
  line-height: 2;
  border-color:#000;
}


  .logo {
    width: 70px;
    height: 70px; 
  }
  
  
  
  
  a{
    color:var(--bs-primary)
    
  }
  a:hover{
    color:var(--bs-hover-color)
  }
  
  
  /* 選單相關色彩 */
  .navbar{
    background-color: var(--bs-nav-color);
  }
  .navbar-nav{
    background-color:var(--bs-rwd-nav-bg-color);
  }
  .navbar-toggler span {
    background:var(--bs-hamburger-close);
  }
  .navbar-toggler.open span{
    background:var(--bs-hamburger-open);
  }
  .nav-link{
   color: var(--bs-nav-link-color); 
  }

  .scrollNav{
    background-color:var(--bs-scroll-nav-bg-color)!important;
  }
  .scrollNav .nav-link{
    color:var(--bs-scroll-nav-link-color);
  }
  .mobileNav  .nav-link{
    color:var(--bs-mobileNav-link-color) !important;
  }
  .nav-link:hover{
    color:var(--bs-hover-color) !important;
  }


  /* hover 色彩*/
.hover__bg--primary{
    transition: all 0.5s;
    &:hover{
        background:var(--bs-primary);
        color:#fff;
    }
}
.hover__bg--secondary{
    transition: all 0.5s;
    &:hover{
      background:var(--bs-secondary);
        color:#fff;
    }
}
.hover__text--primary{
  transition: all 0.5s;
  &:hover{
  
      color:var(--bs-primary);
  }
}
.hover__text--secondary{
  transition: all 0.5s;
  &:hover{
      color:var(--bs-secondary);
  }
}
